/* Style the tab */
.tab {
  overflow: hidden;
  background-color: #f6f6f6;
  border: none;
}

/* Style the buttons inside the tab */
.tab button {
  font-family: "bold", sans-serif;
  background-color: #f6f6f6;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #c0aa83;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border-top: none;
}

.tabcontent h3 {
  font-family: "bold", sans-serif;
  padding: 50px 0 10px 0;
  margin: 0px;
  text-align: left;
}

.tabcontent p {
  font-family: "light", sans-serif;
  font-weight: 500;
  margin: 0px;
  text-align: left;
}

.l-container {
  width: 90%;
  max-width: 1300px;
